
function Cart(props) {
    const {cart} = props
    let total = 0;
    cart.forEach(product=>{
        total += product.price
    })
    return (
        <div>
            <h3>Order Summery</h3>
            <h5>Items Ordered: {cart.length}</h5>
            <h4>Total Price: {total}</h4>
        </div>
    )
}

export default Cart
